import React from 'react';
import { Host } from '@/types';
import { Edit2, Trash2, Home, Phone, MapPin } from 'lucide-react';
import { Card, CardBody } from '@/components/ui/Card';

interface HostListProps {
  hosts: Host[];
  onEdit: (host: Host) => void;
  onDelete: (name: string) => void;
}

export const HostList: React.FC<HostListProps> = ({ hosts, onEdit, onDelete }) => {
  if (hosts.length === 0) {
    return (
      <div className="text-center py-12 text-gray-500 dark:text-gray-400">
        <p className="text-lg font-medium">Aucun contact d'accueil</p>
        <p className="text-sm mt-2">Ajoutez votre premier contact pour commencer</p>
      </div>
    );
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
      {hosts.map((host) => (
        <Card key={host.nom} hoverable>
          <CardBody>
            <div className="flex items-start justify-between">
              <div className="flex-1">
                <h3 className="font-semibold text-gray-900 dark:text-white flex items-center gap-2">
                  <Home className="w-4 h-4" />
                  {host.nom}
                </h3>
                {host.telephone && (
                  <p className="text-sm text-gray-600 dark:text-gray-400 mt-2 flex items-center gap-2">
                    <Phone className="w-3 h-3" />
                    {host.telephone}
                  </p>
                )}
                {host.address && (
                  <p className="text-sm text-gray-500 dark:text-gray-400 mt-1 flex items-start gap-2">
                    <MapPin className="w-3 h-3 mt-0.5 flex-shrink-0" />
                    <span className="line-clamp-2">{host.address}</span>
                  </p>
                )}
              </div>
              <div className="flex gap-2">
                <button
                  onClick={() => onEdit(host)}
                  className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
                  title="Modifier"
                >
                  <Edit2 className="w-4 h-4 text-blue-600" />
                </button>
                <button
                  onClick={() => onDelete(host.nom)}
                  className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
                  title="Supprimer"
                >
                  <Trash2 className="w-4 h-4 text-red-600" />
                </button>
              </div>
            </div>
          </CardBody>
        </Card>
      ))}
    </div>
  );
};
