import React, { useState, useEffect } from 'react';
import { Modal } from '@/components/ui/Modal';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Speaker } from '@/types';
import { useData } from '@/contexts/DataContext';
import { useToast } from '@/contexts/ToastContext';
import { generateUUID } from '@/utils/uuid';

interface SpeakerFormModalProps {
  isOpen: boolean;
  onClose: () => void;
  speaker?: Speaker;
}

export const SpeakerFormModal: React.FC<SpeakerFormModalProps> = ({ isOpen, onClose, speaker }) => {
  const { addSpeaker, updateSpeaker } = useData();
  const { addToast } = useToast();
  const [formData, setFormData] = useState<{
    nom: string;
    congregation: string;
    gender: 'male' | 'female' | 'couple';
  }>({
    nom: '',
    congregation: '',
    gender: 'male',
  });

  useEffect(() => {
    if (speaker) {
      setFormData({
        nom: speaker.nom,
        congregation: speaker.congregation,
        gender: speaker.gender || 'male',
      });
    } else {
      setFormData({ nom: '', congregation: '', gender: 'male' });
    }
  }, [speaker, isOpen]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    try {
      if (speaker) {
        await updateSpeaker({ ...speaker, ...formData });
        addToast('Orateur modifi√©', 'success');
      } else {
        await addSpeaker({
          id: generateUUID(),
          ...formData,
          talkHistory: [],
        });
        addToast('Orateur ajout√©', 'success');
      }
      onClose();
    } catch (error) {
      addToast('Erreur lors de la sauvegarde', 'error');
    }
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose} size="md">
      <form onSubmit={handleSubmit} className="space-y-4">
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
          {speaker ? 'Modifier l\'orateur' : 'Ajouter un orateur'}
        </h3>

        <Input
          label="Nom"
          value={formData.nom}
          onChange={(e) => setFormData({ ...formData, nom: e.target.value })}
          required
        />

        <Input
          label="Congr√©gation"
          value={formData.congregation}
          onChange={(e) => setFormData({ ...formData, congregation: e.target.value })}
          required
        />

        <div>
          <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Genre
          </label>
          <select
            value={formData.gender}
            onChange={(e) => setFormData({ ...formData, gender: e.target.value as 'male' | 'female' | 'couple' })}
            className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-800 text-gray-900 dark:text-white"
            title="Genre"
          >
            <option value="male">Homme</option>
            <option value="female">Femme</option>
            <option value="couple">Couple</option>
          </select>
        </div>

        <div className="flex gap-2 justify-end pt-4">
          <Button type="button" variant="ghost" onClick={onClose}>
            Annuler
          </Button>
          <Button type="submit">
            {speaker ? 'Modifier' : 'Ajouter'}
          </Button>
        </div>
      </form>
    </Modal>
  );
};
