import React, { useState, useEffect } from 'react';
import { Modal } from '@/components/ui/Modal';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Host } from '@/types';
import { useData } from '@/contexts/DataContext';
import { useToast } from '@/contexts/ToastContext';

interface HostFormModalProps {
  isOpen: boolean;
  onClose: () => void;
  host?: Host;
}

export const HostFormModal: React.FC<HostFormModalProps> = ({ isOpen, onClose, host }) => {
  const { addHost, updateHost } = useData();
  const { addToast } = useToast();
  const [formData, setFormData] = useState({
    nom: '',
    telephone: '',
    address: '',
    gender: 'male' as 'male' | 'female' | 'couple',
  });

  useEffect(() => {
    if (host) {
      setFormData({
        nom: host.nom,
        telephone: host.telephone || '',
        address: host.address || '',
        gender: host.gender || 'male',
      });
    } else {
      setFormData({ nom: '', telephone: '', address: '', gender: 'male' });
    }
  }, [host, isOpen]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    try {
      if (host) {
        await updateHost(host.nom, formData);
        addToast('Contact modifi√©', 'success');
      } else {
        await addHost({ ...formData, nom: formData.nom, gender: formData.gender });
        addToast('Contact ajout√©', 'success');
      }
      onClose();
    } catch (error) {
      addToast('Erreur lors de la sauvegarde', 'error');
    }
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose} size="md">
      <form onSubmit={handleSubmit} className="space-y-4">
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
          {host ? 'Modifier le contact' : 'Ajouter un contact'}
        </h3>

        <Input
          label="Nom"
          value={formData.nom}
          onChange={(e) => setFormData({ ...formData, nom: e.target.value })}
          required
        />

        <Input
          label="T√©l√©phone"
          type="tel"
          value={formData.telephone}
          onChange={(e) => setFormData({ ...formData, telephone: e.target.value })}
        />

        <Input
          label="Adresse"
          value={formData.address}
          onChange={(e) => setFormData({ ...formData, address: e.target.value })}
        />

        <div>
          <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Genre
          </label>
          <select
            value={formData.gender}
            onChange={(e) => setFormData({ ...formData, gender: e.target.value as 'male' | 'female' | 'couple' })}
            className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-800 text-gray-900 dark:text-white"
            title="Genre"
          >
            <option value="male">Homme</option>
            <option value="female">Femme</option>
            <option value="couple">Couple</option>
          </select>
        </div>

        <div className="flex gap-2 justify-end pt-4">
          <Button type="button" variant="ghost" onClick={onClose}>
            Annuler
          </Button>
          <Button type="submit">
            {host ? 'Modifier' : 'Ajouter'}
          </Button>
        </div>
      </form>
    </Modal>
  );
};
