import React from 'react';
import { Speaker, Visit } from '@/types';
import { Edit2, Trash2, Star } from 'lucide-react';
import { Card, CardBody } from '@/components/ui/Card';

interface SpeakerListProps {
  speakers: Speaker[];
  onEdit: (speaker: Speaker) => void;
  onDelete: (id: string) => void;
  onFeedback?: (visit: Visit) => void;
}

export const SpeakerList: React.FC<SpeakerListProps> = ({ speakers, onEdit, onDelete }) => {
  if (speakers.length === 0) {
    return (
      <div className="text-center py-12 text-gray-500 dark:text-gray-400">
        <p className="text-lg font-medium">Aucun orateur</p>
        <p className="text-sm mt-2">Ajoutez votre premier orateur pour commencer</p>
      </div>
    );
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
      {speakers.map((speaker) => (
        <Card key={speaker.id} hoverable>
          <CardBody>
            <div className="flex items-start justify-between">
              <div className="flex-1">
                <h3 className="font-semibold text-gray-900 dark:text-white">{speaker.nom}</h3>
                <p className="text-sm text-gray-500 dark:text-gray-400">{speaker.congregation}</p>
                {speaker.talkHistory && speaker.talkHistory.length > 0 && (
                  <div className="mt-2 flex items-center gap-1 text-xs text-gray-600 dark:text-gray-400">
                    <Star className="w-3 h-3" />
                    <span>{speaker.talkHistory.length} discours</span>
                  </div>
                )}
              </div>
              <div className="flex gap-2">
                <button
                  onClick={() => onEdit(speaker)}
                  className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
                  title="Modifier"
                >
                  <Edit2 className="w-4 h-4 text-blue-600" />
                </button>
                <button
                  onClick={() => onDelete(speaker.id)}
                  className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
                  title="Supprimer"
                >
                  <Trash2 className="w-4 h-4 text-red-600" />
                </button>
              </div>
            </div>
          </CardBody>
        </Card>
      ))}
    </div>
  );
};
